/* Global style */

:root {
  --primary-color: #8ee4af;
  --secondary-color: #379683;
  --tertiary-color: #edf5e1;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* body style */

body {
  background-color: var(--primary-color);
}

/* header/nav style */

header {
  background-color: var(--secondary-color);
  width: 100%;
  padding: 20px 35px;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  top: 0;
  background-size: cover;
  background-position: center;
  background-attachment: fixed;
  background-position: 80%;
  z-index: 9999;
}

header h1 {
  font-weight: bolder;
  color: var(--tertiary-color);
  font-size: 40px;
  margin: 0;
  text-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
}

.header-link {
  text-decoration: none;
  color: var(--tertiary-color);
  font-style: normal;
}

header ul, footer address ul {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: center;
  list-style: none;
}

footer address ul {
  justify-content: center;
}

.tab {
  padding: 10px 15px;
  font-weight: lighter;
  font-size: 1.55vw;
  text-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
}

.tab:hover {
  background: var(--primary-color);
  color: var(--secondary-color);
  border-radius: 15px;
  text-shadow: none;
}

.active {
  background: var(--primary-color);
  color: var(--secondary-color);
  border-radius: 15px;
  text-shadow: none;
}

/* hero style */

.hero {
  width: 95%;
  margin: 0 auto;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.photo {
  flex-direction: column;
}

.photo img {
  border-radius: 15px;
  max-width: 100%;
  height: 275px;
  width: 275px;
  object-fit: cover;
  margin: 20px auto 20px auto;
}

.proj {
  width: 95%;
  margin: 0 auto;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  text-align: center;
}

/* about me style */

.section {
  background-color: var(--secondary-color);
  margin: 0 auto 20px auto;
  width: 95%;
  border-radius: 15px;
}

.top {
  margin: 20px auto 20px auto;
}

.title {
  text-align: center;
  color: var(--tertiary-color);
  border-bottom: 3px solid var(--primary-color);
  width: 20%;
  margin: 0 auto;
}

.section p {
  color: var(--tertiary-color);
  text-align: center;
  margin: 0px auto;
  padding: 15px;
  font-size: 1.55vw;
}

.resume-list {
  color: var(--tertiary-color);
  text-align: left;
  margin: 0px auto 0px 20px;
  padding: 15px;
  font-size: 1.55vw;
}

/* work style */

.work {
  margin: 20px auto 20px auto;
}

.work-cards {
  /* display: flex; */
  /* flex-wrap: wrap; */
  width: 50%;
  margin: 20px auto 20px auto;
  justify-content: center;
}

.center {
  text-align: center;
}

.left {
  margin: 15px auto 0 auto;
  text-align: left;
}

.card {
  flex-direction: column;
}

.card img {
  border-radius: 15px;
  max-width: 100%;
  height: 275px;
  width: 275px;
  object-fit: fill;
  margin: 15px auto 0 auto;
}

.photo-text {
  position: relative;
  text-align: center;
  color: var(--tertiary-color);
}

.photo-text a {
  text-decoration: none;
}

form {
  width: 40%;
}

input {
  font-size: 1rem;
  padding: 0.25rem;
  border: 1px solid var(--dark);
  outline: none;
  border-radius: 3px;
  transition: box-shadow 0.2s;
}

input:focus {
  border: 1px solid var(--secondary);
  box-shadow: 0 0 .25rem rgba(0, 0, 0, .5);
}

textarea {
  width: 600px;
  height: 120px;
  border: 3px solid #cccccc;
  padding: 5px;
}
/* media queries */

/* break @1100 */

@media screen and (max-width: 1158px) {
  .photo img {
    width: 200px;
    height: 200px;
  }
  .card img {
    width: 200px;
    height: 200px;
  }
  .section p {
    font-size: 2vw;
  }
  header nav ul li a, footer address ul li a {
    font-size: 2vw;
  }
}

/* break @842 */

@media screen and (max-width: 843px) {
  .hero {
    width: 95%;
    margin: 0 auto;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
  }
  .photo img {
    width: 150px;
    height: 150px;
  }
  .card img {
    width: 150px;
    height: 150px;
  }
  .section p {
    font-size: 2.5vw;
  }
  header nav ul li a, footer address ul li a {
    font-size: 2.5vw;
  }
}

@media screen and (max-width: 632px) {
  .hero {
    width: 95%;
    margin: 0 auto;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
  }
  .photo img {
    width: 110px;
    height: 110px;
  }
  .card img {
    width: 120px;
    height: 120px;
  }
  .section p {
    font-size: 4vw;
  }
  header nav ul li a, footer address ul li a {
    font-size: 4vw;
  }
}

@media screen and (max-width: 505px) {
  .hero {
    width: 95%;
    margin: 0 auto;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
  }
  .photo img {
    width: 100px;
    height: 100px;
  }
  .section p {
    font-size: 5vw;
  }
  header nav ul li a, footer address ul li a {
    font-size: 5vw;
  }
}

@media screen and (max-width: 422px) {
  .hero {
    width: 95%;
    margin: 0 auto;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
  }
  .photo img {
    width: 80px;
    height: 80px;
  }
}

@media screen and (max-width: 338px) {
  .hero {
    width: 95%;
    margin: 0 auto;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
  }
  .photo img {
    width: 108px;
    height: 108px;
  }
}